.blog-post {
    width: 70%;
    height: 40%;
    padding: 1%;
    float: none;
    margin-left: auto;
    margin-right: auto;
    margin-top: 5%;
    margin-bottom: 10%;
}

.blog-header {
    display: flex;
    width: 96%;
    height: 16%;
    padding: 2%;
    background-color: black;
    border: 5px solid #34495E;
    border-top-right-radius: 25px;
}

.blog-content {
    width: 100%;
    height: 64%;
    background-color: black;
    border: 5px solid #34495E;
    border-top: none;
}

.blog-text {
    font-family: 'Courier New', Courier, monospace;
    color: yellow;
}

.blog-title {
    display: inline-block;
    width: 50%;
    height: 100%;
    text-align: left;
    margin-top: 2%;
    margin-left: 2%;
}

.blog-title-admin {
    width: 45%;
}

.blog-author {
    display: inline-block;
    width: 50%;
    height: 100%;
    text-align: right;
    margin-top: 2%;
    margin-right: 2%;
}

.blog-author-admin {
    width: 45%;
}

.blog-delete {
    display: inline-block;
    width: 12%;
    height: 100%;
    color: red;
    border-radius: 15px;
    border: 1px solid red;
    font-weight: bold;
}

.blog-delete:hover {
    color: black;
    background-color: red;
}

.blog-description {
    text-align: left;
    width: 80%;
    height: 65%;
    padding-top: 2%;
    padding-left: 2%;
}

.blog-footer {
    display: flex;
    width: 96%;
    height: 16%;
    padding: 2%;
    background-color: black;
    border: 5px solid #34495E;
    border-bottom-left-radius: 25px;
    border-top: none;
}

.blog-comments {
    display: inline-block;
    width: 50%;
    height: 100%;
    text-align: left;
    margin-top: 2%;
    margin-left: 2%;
}

.blog-link {
    float: bottom;
    width: 90%;
    height: 10%;
    text-align: right;
    margin-right: 5%;
    margin-bottom: 5%;
}

.blog-link:hover {
    color: blue;
    text-decoration: underline;
}