body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#root {
  height: 100%;
  width: 100%;
}

html, body {
  height: 100%;
  width: 100%;
}

.App {
  text-align: center;
  height: 100%;
  width: 100%;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
.left-sidebar {
  display: -webkit-flex;
  display: flex;
  float: left;
  width: 20%;
  height: 100%;
}

.page-container {
  width: 100%;
  height: 100%;
}

.right-sidebar {
  display: -webkit-flex;
  display: flex;
  float: left;
  width: 20%;
  height: 100%;
}
.blog-container {
    display: flexbox;
    float: left;
    width: 60%;
    height: 100%;
    background-color: #283747;
    border-radius: 2%;
    -webkit-align-content: center;
            align-content: center;
    overflow-x: auto;
  }

  .blog-list-action-bar {
    width: 70%;
    height: 5%;
    padding-top: 1%;
    padding-bottom: 1%;
    border-radius: 25px;
    border: 5px solid #34495E;
    background-color: black;
    margin-top: 5%;
    margin-left: auto;
    margin-right: auto;
    font-size: 20px;
    font-weight: bold;
    color: green;
}

.blog-list-action-bar:hover {
  color: black;
  background-color: green;
}
.blog-post {
    width: 70%;
    height: 40%;
    padding: 1%;
    float: none;
    margin-left: auto;
    margin-right: auto;
    margin-top: 5%;
    margin-bottom: 10%;
}

.blog-header {
    display: -webkit-flex;
    display: flex;
    width: 96%;
    height: 16%;
    padding: 2%;
    background-color: black;
    border: 5px solid #34495E;
    border-top-right-radius: 25px;
}

.blog-content {
    width: 100%;
    height: 64%;
    background-color: black;
    border: 5px solid #34495E;
    border-top: none;
}

.blog-text {
    font-family: 'Courier New', Courier, monospace;
    color: yellow;
}

.blog-title {
    display: inline-block;
    width: 50%;
    height: 100%;
    text-align: left;
    margin-top: 2%;
    margin-left: 2%;
}

.blog-title-admin {
    width: 45%;
}

.blog-author {
    display: inline-block;
    width: 50%;
    height: 100%;
    text-align: right;
    margin-top: 2%;
    margin-right: 2%;
}

.blog-author-admin {
    width: 45%;
}

.blog-delete {
    display: inline-block;
    width: 12%;
    height: 100%;
    color: red;
    border-radius: 15px;
    border: 1px solid red;
    font-weight: bold;
}

.blog-delete:hover {
    color: black;
    background-color: red;
}

.blog-description {
    text-align: left;
    width: 80%;
    height: 65%;
    padding-top: 2%;
    padding-left: 2%;
}

.blog-footer {
    display: -webkit-flex;
    display: flex;
    width: 96%;
    height: 16%;
    padding: 2%;
    background-color: black;
    border: 5px solid #34495E;
    border-bottom-left-radius: 25px;
    border-top: none;
}

.blog-comments {
    display: inline-block;
    width: 50%;
    height: 100%;
    text-align: left;
    margin-top: 2%;
    margin-left: 2%;
}

.blog-link {
    float: bottom;
    width: 90%;
    height: 10%;
    text-align: right;
    margin-right: 5%;
    margin-bottom: 5%;
}

.blog-link:hover {
    color: blue;
    text-decoration: underline;
}
.header-container {
    display: flexbox;
    width: 70%;
    height: 10%;
    border: 5px solid #34495E;
    background-color: black;
    border-top-right-radius: 25px;
    border-bottom-left-radius: 25px;
    margin-top: 5%;
    margin-left: 12.5%;
    padding: 2.5%;
}

.search-container {
    display: -webkit-flex;
    display: flex;
    width: 100%;
}

.search-input-container {
    width: 80%;
    height: 100%;
}

.search-button-container {
    width: 20%;
    height: 100%;
}

.input-search {
    width: 95%;
    height: 30px;
    font-size: 20px;
}

.button-search {
    float: left;
    width: 90%;
    height: 36px;
}

.login-button-container {
    width: 100%;
    height: 30%;
    padding-top: 1%;
    margin-bottom: 1%;
}

.login-button-nav {
    float: right;
}


.blog-article-container {
    display: flexbox;
    width: 60%;
    height: 100%;
    float: left;
    background-color: #283747;
    border-radius: 2%;
    -webkit-align-content: center;
            align-content: center;
    overflow-x: auto;
}

.blog-article {
    display: flexbox;
    width: 80%;
    height: 100%;
    padding: 5%;
    margin-left: auto;
    margin-right: auto;
    margin-top: 5%;
    margin-bottom: 5%;
}

.blog-text {
    font-family: 'Courier New', Courier, monospace;
    color: yellow;
}

.blog-article-header {
    display: -webkit-flex;
    display: flex;
    width: 100%;
    height: 10%;
    border: 5px solid #34495E;
    background-color: black;
    border-top-right-radius: 25px;
}

.blog-article-title {
    display: inline-block;
    width: 45%;
    height: 100%;
    margin-left: 5%;
    margin-top: 3%;
    font-size: 30px;
    text-align: left;
}

.blog-article-author {
    display: inline-block;
    width: 45%;
    height: 100%;
    margin-top: 4%;
    margin-right: 5%;
    font-size: 20px;
    text-align: right;
}

.blog-article-edit {
    display: inline-block;
    width: 12%;
    height: 70%;
    color: limegreen;
    border-radius: 15px;
    border: 1px solid limegreen;
    margin-top: 10px;
    margin-right: 10px;
    font-weight: bold;
}

.blog-article-edit:hover {
    color: black;
    background-color: limegreen;
}

.blog-article-submit {
    display: inline-block;
    width: 12%;
    height: 70%;
    color: turquoise;
    border-radius: 15px;
    border: 1px solid turquoise;
    margin-top: 10px;
    margin-right: 10px;
    font-weight: bold;
}

.blog-article-submit:hover {
    color: black;
    background-color: turquoise;
}

.blog-article-content {
    width: 90%;
    min-height: 80%;
    padding: 5%;
    border: 5px solid #34495E;
    border-top: none;
    background-color: black;
    border-bottom-left-radius: 25px;
    text-align: left;
}

.action-bar {
    width: 100%;
    height: 5%;
    padding-top: 1%;
    padding-bottom: 1%;
    border-radius: 25px;
    border: 5px solid #34495E;
    background-color: black;
    margin-bottom: 10px;
    font-size: 20px;
}

.exit-link {
    width: 100%;
    margin-top: 5px;
}

.exit-link:hover {
    opacity: 0.6;
}

.blog-article-textarea {
    width: 100%;
}

.blog-article-description {
    width: 100%;
}

.blog-article-textarea-header {
    font-size: 20px;
    color: yellow;
    padding: 10px;
    padding-left: 5px;
}
.page-container {
    height: 100%;
    width: 100%;
}

.login-dialog {
    position: fixed;
    top: 30%;
    left: 35%;
    width: 30%;
    height: 40%;
    border: 5px solid #34495E;
    border-top-right-radius: 25px;
    border-bottom-left-radius: 25px;
    background-color: black;
}

.input-container {
    width: 80%;
    height: 50%;
    margin-left: 10%;
    margin-top: 10%;
}

.input-grid {
    width: 100%;
    height: 60%;
}

.input-row {
    width: 100%;
}

.input-field {
    height: 20px;
}

.input-label {
    float: right;
    color:yellow;
}

.submit-input {
    width: 100%;
    height: 20%;
    margin-top: 10px;
}

.login-button {
    float: right;
    width: 100px;
    height: 30px;
}

.login-header {
    height: 10%;
    width: 100%;
    -webkit-align-content: center;
            align-content: center;
    color: yellow;
}
